// vmvuex
module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		/* config */
		config.baseURL = 'https://jikepic.jikebox.cn'; /*  */


		config.timeout = 60000;

		return config
	})

	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		// dataundefined{}
		config.data = config.data || {}
		console.log(config)
		config.header['Content-Type'] = 'application/x-www-form-urlencoded'
		// console.log(uni.getStorageSync('storage_key'))
		// customtoken
		if (uni.getStorageSync('storage_key')) {
			// vmvuexvm.$store.state
			config.header.token = uni.getStorageSync('storage_key').token.token
		}
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use((response) => {
		/*  async await */
		const data = response.data
		console.log(response.statusCode)
		// 
		// const custom = response.config?.custom
		if (data.code != 1) {
			// customtoastfalsetoast
			if (data.msg) {
				uni.$u.toast(data.msg)
			}
			// // catchreject
			// if (custom?.catch) {
			//  return Promise.reject(data)
			// } else {
			//  // pendingpromisecatch
			//  return new Promise(() => {})
			// }

		}
		return data === undefined ? {} : data
	}, (response) => {
		//  statusCode !== 200
		if (response.statusCode == 401) {
			uni.$u.route('/pages/jy_Sign/jy_Sign')
		}
		return Promise.reject(response)
	})
}
